html{font:500 14px/150% 'Open Sans',sans-serif}*,::after,::before{box-sizing:inherit}body{background:#f0f0f0}:root{--base-color:#0066ff}.signup{background:0 0;color:#fff;border:0;border-radius:3px;padding:0 16px;height:42px;text-transform:uppercase;font-weight:600;outline:0}.login{background-color:transparent;color:#fff;border:0;border:1px solid #e5bb3d;border-radius:3px;padding:0 16px;height:42px;text-transform:uppercase;font-weight:600;outline:0}.btn,.err--btn,.primary--btn,.warning--btn{padding:0 16px;height:36px;background:linear-gradient(to left,#00ae14,#007b0e);color:#fff;font-weight:600;text-transform:uppercase;border-radius:3px;border:0;box-shadow:0 15px 30px rgba(0,0,0,.1),0 5px 15px rgba(0,0,0,.15);transition:all .2s ease-in-out}.btn:hover,.err--btn:hover,.primary--btn:hover,.warning--btn:hover{cursor:pointer}.btn:focus,.err--btn:focus,.primary--btn:focus,.warning--btn:focus{outline:0}.btn:active,.err--btn:active,.primary--btn:active,.warning--btn:active{box-shadow:0 5px 10px rgba(0,0,0,.1),0 1px 5px rgba(0,0,0,.15);background:linear-gradient(to left,#00ae14,#00620b);transition:all .15s ease-in-out}.primary--btn{background:linear-gradient(to left,#06f,#0052cc);color:#fff}.primary--btn:active{background:linear-gradient(to left,#06f,#0047b3);transition:all .15s ease-in-out}.err--btn{background:linear-gradient(to left,tomato,#ff3814);color:#fff}.err--btn:active{background:linear-gradient(to left,tomato,#fa2600);transition:all .15s ease-in-out}.warning--btn{background:linear-gradient(to left,#e5bb3d,#d3a51c);color:#fff}.warning--btn:active{background:linear-gradient(to left,#e5bb3d,#bc9419);transition:all .15s ease-in-out}.li0:hover{background-color:#ddd;cursor:pointer}.currency-map-section>ul li:first-of-type{background:linear-gradient(to left,#06f,#0052cc);color:#fff}.sales>ul li:first-of-type{background:linear-gradient(to left,#00ae14,#007b0e);color:#fff}.nav-section>ul li>a{text-decoration:none;color:#fff}.section-assigner{width:var(--base-width);margin:0 